.input-label {
  padding: 1em;
}
.input-label label {
  display: block;
}

.flex-container {
  display: flex;
  justify-content: space-around;
  position: sticky;
  top: 0;
  height: 300;
}

body {
  min-height: 10000px;
}

@media screen and (max-width: 900px) {
  .flex-container {
    display: block;
  }
}
